const util = require('@/util/util.js');
import refresh from '@/components/refresh.vue';
import navTab from '@/components/navTab.vue';
import topBar from "@/components/account/index1.vue";
import uniGrid from '@/components/uni-grid/uni-grid.vue'
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue'
export default {
	components: {
		navTab,
		topBar,
		uniGrid,
		uniGridItem
	},
	data() {
		return {
			detailist: ["可提现", "待提现", "产品额度"],
			tabCurrentIndex: 0,
			currentPage: 'index',
			tabTitle: ['全部商品', '促销活动'], //导航栏格式 --导航栏组件
			currentTab: 0, //sweiper所在页
			pages: [1, 1, 1, 1], //第几个swiper的第几页
			list: [
				[1, 2, 3, 4, 5, 6],
				['a', 'b', 'c', 'd', 'e', 'f']
			], //数据格式
		}
	},
	methods: {
		changeTab(index) {
			this.tabCurrentIndex = index
		},
		// 其他请求事件 当然刷新和其他请求可以写一起 多一层判断。
		isRequest(pages) {
			return new Promise((resolve, reject) => {
				this.pages[this.currentTab]++
				var that = this
				setTimeout(() => {
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: `请求第${that.currentTab + 1 }个导航栏的第${that.pages[that.currentTab]}页数据成功`
					})
					let newData = ['新数据1', '新数据2', '新数据3']
					resolve(newData)
				}, 1000)
			})
		},
		// swiper 滑动
		swiperTab: function(e) {
			var index = e.detail.current //获取索引
			if (this.tabTitle.length <= 5) {
				this.$refs.navTab.navClick(index)
			} else {
				this.$refs.navTab.longClick(index)
			}
		},
		// 加载更多 util.throttle为防抖函数
		lower1: util.throttle(function(e) {
			console.log(`加载${this.currentTab}`) //currentTab表示当前所在页数 根据当前所在页数发起请求并带上page页数
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			this.isRequest().then((res) => {
				let tempList = this.list
				tempList[this.currentTab] = tempList[this.currentTab].concat(res)
				console.log(tempList)
				this.list = tempList
				this.$forceUpdate() //二维数组，开启强制渲染
			})
		}, 300),
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e);
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
		isRefresh() {
			setTimeout(() => {
				uni.showToast({
					icon: 'success',
					title: '刷新成功'
				})
				this.$refs.refresh.endAfter() //刷新结束调用
			}, 1000)
		},
		goPage(event) {
			var index = event.detail.index;
			switch (index) {
				case 0:
					uni.navigateTo({
						url: '/pages/benefits/meeting'
					});
					break;
				case 1:
					uni.navigateTo({
						url: '/pages/benefits/preferential'
					});
					break;
				case 2:
					uni.navigateTo({
						url: '/pages/benefits/integral'
					});
					break;
				case 3:
					uni.navigateTo({
						url: '/pages/benefits/upvue'
					});
					break;
			}

		},
		goDetail() {
			uni.navigateTo({
				url: '/pages/home/detail'
			});
		}
	}
}
